.inputTextWrapper{
    @apply flex flex-col;
}

.inputText{
    @apply pl-0 border-0 py-0 border-b-[1px] bg-transparent border-b-gray-500 focus-within:ring-0 focus-within:border-b-black mb-4;
}


.inputTextLabel{
    @apply transition ease-in-out duration-200 translate-y-6;
}

/* CheckBoxes */

.checkboxWrapper{
    @apply mt-3;
}

.checkboxCircle{
    @apply rounded-full focus-within:ring-0 h-5 w-5 mr-2 -mt-1;
}

.checkboxNormal{
    @apply focus-within:ring-0 h-5 w-5 mr-2 -mt-1;
}

.checkboxLabel{
    @apply text-justify;
}

/* Google Login */

.googleButton{
    @apply w-full flex justify-center rounded-md border-[1px] border-gray-300 text-gray-500 py-3 mx-auto;
}

.googleImage{
    @apply w-4 h-4 mr-2 mt-1;
}