/* Sonia your tailwind css here */
.aboutBody {
    @apply  rounded-3xl block py-10 px-5 my-5 grid gap-0;
    @apply md:p-10 md:flex md:gap-8 md:grid-cols-2;
    @apply lg:p-20 lg:justify-around lg:gap-16 lg:my-20 lg:mx-auto;
}

.aboutMain {
}

.aboutText {
    @apply break-normal font-bold text-base;
}

.aboutImage {
    @apply w-[84rem];
}

.aboutHeading {
    @apply text-6xl font-black;
}

.figWrapper {
    @apply mb-12;
    @apply md:my-auto;
}

.figImage {
    @apply lg:h-full  my-auto mx-auto w-64;
    @apply lg:w-[25.5rem];
}

.figCaption {
    @apply font-semibold text-xs break-words text-center pt-5 px-3;
}

.founderFigure {
    @apply w-96 my-0 mx-auto;
    @apply lg:h-full;
}

.founderImage{
    @apply rounded-3xl h-full;
}

