.containcard {
    @apply flex items-center  justify-center mt-10;
} 

.divcard {
    @apply rounded-3xl border p-5 shadow-xl w-9/12 bg-white;
}

.headerdiv {
    @apply flex w-full items-center justify-between border-b pb-3;
} 

.fisrtcontent {
    @apply flex items-center space-x-3;
}

.imgstyle{
    @apply h-28 w-28 rounded-full bg-slate-400 mb-4;
}

.textimgstyle{
    @apply mb-3 text-xl font-bold;
}

.textstyle{
    @apply text-sm text-justify font-serif;
}